import { html, TemplateResult } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';

export type TMarketTabPanelTemplateArgs = Partial<HTMLMarketTabPanelElement> & {
  ariaLabelledby?: string;
  content?: string | TemplateResult;
};

export const TabPanelTemplate = ({ ariaLabelledby, hidden, id, content }: TMarketTabPanelTemplateArgs) => html`
  <market-tab-panel aria-labelledby=${ifDefined(ariaLabelledby)} ?hidden=${ifDefined(hidden)} id=${ifDefined(id)}>
    ${content}
  </market-tab-panel>
`;
